import React, { useState } from 'react';
import { GoogleGenerativeAI } from '@google/generative-ai';
import ReactMarkdown from 'react-markdown';
import rehypeHighlight from 'rehype-highlight';
import 'highlight.js/styles/github.css'; // 
import './App.css';

function App() {
  // 
  const [height, setHeight] = useState('');
  const [weight, setWeight] = useState('');
  const [age, setAge] = useState('');
  const [image, setImage] = useState(null);
  const [imageFile, setImageFile] = useState(null);

  // 
  const [bmi, setBmi] = useState(null);
  const [bmiCategory, setBmiCategory] = useState('');

  // AI
  const [healthAdvice, setHealthAdvice] = useState('');
  const [foodAdvice, setFoodAdvice] = useState('');
  const [loading, setLoading] = useState(false);

  // 
  const handleInputChange = (e, setter) => {
    const value = e.target.value.replace(/[^0-9.]/g, '');
    setter(value);
  };

  // 
  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      if (file.size > 5 * 1024 * 1024) {
        alert('5MB');
        return;
      }
      setImageFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setImage(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  // BMI
  const calculateBMI = () => {
    const h = parseFloat(height) / 100;
    const w = parseFloat(weight);
    if (isNaN(h) || isNaN(w) || h <= 0 || w <= 0) {
      alert('');
      return;
    }
    const calculatedBmi = w / (h * h);
    setBmi(calculatedBmi.toFixed(2));
    if (calculatedBmi < 18.5) setBmiCategory('');
    else if (calculatedBmi < 25) setBmiCategory('');
    else if (calculatedBmi < 30) setBmiCategory('');
    else setBmiCategory('');
  };

  // Gemini 2.0 Flash
  const getHealthAdvice = async () => {
    if (!bmi) {
      alert('BMI');
      return;
    }
    setLoading(true);
    try {
      const genAI = new GoogleGenerativeAI(process.env.REACT_APP_GEMINI_API_KEY);
      const model = genAI.getGenerativeModel(
        { model: 'gemini-2.0-flash' }, // Gemini 2.0 Flash
        { apiVersion: 'v1' } // v1
      );
      const prompt = `: ${height}cm: ${weight}kg: ${age}BMI: ${bmi} (${bmiCategory})`;
      const result = await model.generateContent(prompt);
      const response = await result.response;
      setHealthAdvice(response.text());
    } catch (error) {
      console.error('Gemini API:', error);
      setHealthAdvice('API');
    }
    setLoading(false);
  };

  // Gemini 2.0 Flash: 
  const getFoodAdvice = async () => {
    if (!bmi || !imageFile) {
      alert('BMI');
      return;
    }
    setLoading(true);
    try {
      const genAI = new GoogleGenerativeAI(process.env.REACT_APP_GEMINI_API_KEY);
      const model = genAI.getGenerativeModel(
        { model: 'gemini-2.0-flash' }, // Gemini 2.0 Flash
        { apiVersion: 'v1' } // v1
      );

      const reader = new FileReader();
      reader.readAsDataURL(imageFile);
      const base64Image = await new Promise((resolve) => {
        reader.onloadend = () => resolve(reader.result.split(',')[1]);
      });

      const prompt = `: ${height}cm: ${weight}kg: ${age}BMI: ${bmi} (${bmiCategory})`;

      const imagePart = {
        inlineData: {
          data: base64Image,
          mimeType: imageFile.type,
        },
      };

      const result = await model.generateContent([prompt, imagePart]);
      const response = await result.response;
      setFoodAdvice(response.text());
    } catch (error) {
      console.error('Gemini API:', error);
      setFoodAdvice('API');
    }
    setLoading(false);
  };

  // UIGemini 2.0 Flash
  return (
    <div className="App">
      <h1></h1>

      {/*  */}
      <div className="input-form">
        <label>
           (cm):
          <input
            type="text"
            value={height}
            onChange={(e) => handleInputChange(e, setHeight)}
            placeholder=": 170"
          />
        </label>
        <br />
        <label>
           (kg):
          <input
            type="text"
            value={weight}
            onChange={(e) => handleInputChange(e, setWeight)}
            placeholder=": 65"
          />
        </label>
        <br />
        <label>
           ():
          <input
            type="text"
            value={age}
            onChange={(e) => handleInputChange(e, setAge)}
            placeholder=": 30"
          />
        </label>
        <br />
        <button onClick={calculateBMI}>BMI</button>
      </div>

      {/*  */}
      <div className="image-upload">
        <label>
          :
          <input
            type="file"
            accept="image/*"
            onChange={handleImageChange}
          />
        </label>
      </div>

      {/*  */}
      {image && (
        <div className="image-preview">
          <h3>:</h3>
          <img src={image} alt="" />
        </div>
      )}

      {/* BMI */}
      {bmi && (
        <div className="bmi-result">
          <h3>BMI</h3>
          <p>BMI: {bmi}</p>
          <p>: {bmiCategory}</p>
        </div>
      )}

      {/*  */}
      {bmi && (
        <div className="advice-section">
          <button onClick={getHealthAdvice} disabled={loading}>
            {loading ? '...' : ' (Gemini 2.0 Flash)'}
          </button>
        </div>
      )}

      {/*  */}
      {healthAdvice && (
        <div className="advice-result">
          <h3>AI</h3>
          <ReactMarkdown rehypePlugins={[rehypeHighlight]}>{healthAdvice}</ReactMarkdown>
        </div>
      )}

      {/*  */}
      {bmi && image && (
        <div className="advice-section">
          <button onClick={getFoodAdvice} disabled={loading}>
            {loading ? '...' : ' (Gemini 2.0 Flash)'}
          </button>
        </div>
      )}

      {/*  */}
      {foodAdvice && (
        <div className="advice-result">
          <h3>AI</h3>
          <ReactMarkdown rehypePlugins={[rehypeHighlight]}>{foodAdvice}</ReactMarkdown>
        </div>
      )}
    </div>
  );
}

export default App;